#container {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
#img {
  border-radius: 50%;
  position: absolute;
  right: 4.5rem;
  top: -3rem;
  border: 4px solid rgb(54, 54, 54);

  padding: 0.2rem;
  animation: myrotate 5s infinite linear;
}
@media (min-width: 768px) {
  #img {
    position: absolute;
    top: -3rem;
    right: auto;
  }
}
@keyframes myrotate {
  100% {
    transform: rotate(360deg);
  }
}

.title {
  align-self: flex-start;
}
#controls {
  align-self: flex-start;
}
